<template>
    <!--创建定单内容-->
    <div class="_shoporder d-flex justify-content-center align-items-start ">
        <div id="_order-box" class="_order-box card card-body px-0 mx-0 ">
            <!--设置标题栏的后退功能-->
            <div class="" style="position:absolute;left:0;top:0;">
                <div class="txt-subtitle txt-subtitle-space  px-2 px-sm-3 " style="font-weight:bold"
                     @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px;color:#1f1f1f"></i>
                    <span>返回</span>
                </div>
            </div>
            <!--订单提示栏-->
            <div class="d-flex flex-column justify-content-center pb-2 pb-sm-3">
                <span class="txt-subtitle-space txt-body px-2 px-sm-3 " style="font-weight:bold">待付款</span>
                <span class="txt-subbody py-1 text-danger">请在30分钟内支付，逾期订单将失效</span>
            </div>
            <!--订单地址栏-->
            <div class="row ">
                <div class="col-10 d-flex flex-row justify-content-start align-items-center px-4">
                    <i class="iconfont icon-shouhuodizhi" style="font-size:25px;color:red;"></i>
                    <div class="txt-subbody text-truncate d-flex flex-column align-items-start px-2 ">
                        <div>{{$store.getters.getAddressUserName}} {{$store.getters.getAddressUserPhone}}
                            {{$store.getters.getAddressUserProvince}} {{$store.getters.getAddressUserCity}}
                            {{$store.getters.getAddressUserCounty}}
                        </div>
                        <div> {{$store.getters.getAddressUserDetail}}</div>
                    </div>
                </div>
                <div class="col-2 col-md-2 px-0 d-flex justify-content-start align-items-center" style=" ">
                    <span class="px-2 mr-3 btn border-danger round rounded-lg text-danger "
                          @click="$router.push({path:'/address'})">修改</span>
                </div>
            </div>
            <!--订单商品栏-->
            <goods-item :allList = "carList" :path="$route.fullPath">
                <template v-slot:numSlot="subdata">
                    <span class="_myNum txt-body px-5  ">x{{subdata.item.num}}</span>
                </template>
                <template v-slot:bottom="subdata">
                    <div class="txt-subbody d-flex justify-content-end align-items-end py-2">
                        <div class="px-4">
                            <span>应付：</span>
                            <small class="text-danger">￥</small>
                            <span class="txt-body text-danger ">{{total}}</span>
                            <span class="px-2">(免运费)</span>
                        </div>
                    </div>
                </template>
            </goods-item>
            <!--订单支付方式栏-->
            <ul class="_payMethod txt-body d-inline-block list-unstyled py-3">
                <li class="d-flex justify-content-between px-5 pb-2" @click="onPayMethod(0)">
                    <div class="float-left">
                        <i class="iconfont icon-weixin" style="color:#DEA220;font-size:18px;"></i>
                        <span class="px-2">微信支付</span>
                    </div>
                    <div class="_pay-list-item float-right">
                        <i class="iconfont icon-success" style="color:#736139;font-size:18px;"></i>
                    </div>
                </li>
                <li class="active d-flex justify-content-between px-5 pb-2" @click="onPayMethod(1)">
                    <div class="float-left">
                        <i class="iconfont icon-alipay" style="color:#936AEF;font-size:18px;"></i>
                        <span class="px-2">支付宝</span>
                    </div>
                    <div class="_pay-list-item float-right">
                        <i class="iconfont icon-success" style="color:#736139;font-size:18px;"></i>
                    </div>
                </li>
                <li class="d-flex justify-content-between px-5 pb-2" @click="onPayMethod(2)">
                    <div class=" float-left">
                        <!-- <i class="iconfont icon-QQ" style="color:#1f1f1f;font-size:18px;"></i>-->
                        <img src="~assets/images/pic/icon-qq.png" alt="" style="width:21px;height:25px;">
                        <span class="px-2">QQ钱包</span>
                    </div>
                    <div class="_pay-list-item float-right">
                        <i class="iconfont icon-success" style="color:#736139;font-size:18px;"></i>
                    </div>
                </li>
            </ul>
            <!--订单结算栏-->
            <div id="_calculate" class="_totalBalance bg-white py-1 py-sm-2">
                <div class="_checkBtn  d-flex justify-content-end align-items-center px-2">
                    <div class="mx-3   ">
                        <span class="txt-subbody">合计:</span>
                        <span class="txt-subbody text-danger">￥</span>
                        <span class="txt-subtitle text-danger">{{total}}</span>
                        <span class="txt-subbody text-danger">(免运费)</span>
                    </div>
                    <div class="bg-danger border border-danger text-white rounded rounded-pill px-4 py-2"
                         @click="payFee()">
                        <span>去支付</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--弹窗提示栏-->
    <!-- Modal 组件 （bootstrap 框架里的组件） -->
    <div class="_hidden-background " id="myModalBox">
        <div class="modal fade" id="myModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header py-1">
                        <div class="txt-body modal-title" id="exampleModalLabel">支付二维码</div>
                        <i class="iconfont icon-cancel" style="color:black;font-size:25px;" @click="payCancel"></i>
                    </div>
                    <div class="modal-body p-2">
                        <div class="d-flex flex-column justify-content-around ">
                            <div class="d-flex justify-content-center px-3 align-items-center">
                                <i class="iconfont  " style="color:#936AEF;font-size:25px" :class="selectIcon" v-if="selectIcon.length>0"></i>
                                <img src="~assets/images/pic/icon-qq.png" alt="" style="width:21px;height:25px;" v-else>
                                <span class="px-1  " >打开{{selectName}}支付</span>
                            </div>
                            <div class="d-flex flex-column justify-content-around align-items-center">
                                <img :src="aliyun"
                                     alt="" class="img-fluid" style="max-width:200px;">
                                <span class="text-danger text-center  pt-md-3" style="max-width:180px;">请确认您当前的支付环境安全，再进行支付！</span>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer p-1">
                        <button type="button" class="confirmPay btn btn-primary" @click="submitPay">已经支付</button>
                        <button type="button" class="cancelPay btn btn-primary" data-dismiss="modal"
                                @click="submitPayCancel">
                            放弃支付
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery';
    import {ElMessage, ElLoading} from 'element-plus';
    import {getAddressList} from 'network/address';
    import {getOrderPreview, createOrder, payOrder, payOrderStatus} from 'network/order';
    import GoodsItem from 'components/content/goods/GoodsItem';

    import {useRoute, useRouter} from 'vue-router';
    import {reactive, onMounted, toRefs, computed, ref} from 'vue';
    import {useStore} from 'vuex';

    export default {
        name: "CreateOrder",
        components: {
            GoodsItem,
        },
        setup() {
            const router = useRouter();
            const route = useRoute();
            const store = useStore();

            const state = reactive({
                carList: [],                 // 订单信息
                address: {},                 // 地址信息
                showPay: false,
                orderNo: '',                  // 订单id
                aliyun: '',
                wechat: '',
            });

            let timer = ref(null);            // 订单支付定时器
            let mystatus = ref(false);
            let selectIndex = ref(1);          // 支付方式
            let selectIcon = ref('icon-alipay');
            let selectName = ref('支付宝');

            // 初始化信息
            onMounted(() => {
                let loadingInstance_init = ElLoading.service({fullscreen: false, text: '订单加载中', target: '._order-box'});

                getOrderPreview().then(res => {
                    let address = res.address.filter(n => n.is_default == '1');
                    if (address.length === 0) {
                        state.address = {
                            address: '还没有设置默认地址，靖选择或填写地址信息'
                        }
                    } else {
                        state.address = address[0];
                    }
                    state.carList = res.carts;
                    console.log("createorder-data",state.carList);
                    if ( state.carList.length === 0) {
                        ElMessage.warning('当前没有商品数据');
                        setTimeout(() => {
                            router.go(-1);
                        },500)
                    } else {
                        initAddress(state.carList.address);
                        console.log("createorder-data 默认地址:", res);
                    }
                }).catch(err => {
                    console.log("createorder-err", err);
                    ElMessage.error('获取订单信息失败');
                    setTimeout(() => {
                        //   router.go(-1);
                    })
                });

                if (loadingInstance_init !== null) {
                    loadingInstance_init.close();                // 页面加载中...
                    loadingInstance_init = null;
                }
            });


            // 初始化数据，设置vuex中的内容
            const initAddress = (address) => {
                if (store.getters.getAddressId === undefined || store.getters.getAddressId === 0 ||
                    store.getters.getAddressUserName === undefined ||
                    store.getters.getAddressUserPhone === undefined ||
                    store.getters.getAddressUserProvince === undefined ||
                    store.getters.getAddressUserCity === undefined ||
                    store.getters.getAddressUserCounty === undefined ||
                    store.getters.getAddressUserDetail === undefined) {

                    address.forEach(item => {           // 参数必需符合地址列表格式
                        if (item.is_default) {
                            store.commit('setAddressId', item.id);      // 给vuex提交地址的ID
                            store.commit('setAddressUserName', item.name);
                            store.commit('setAddressUserPhone', item.phone);
                            store.commit('setAddressUserProvince', item.province);
                            store.commit('setAddressUserCity', item.city);
                            store.commit('setAddressUserCounty', item.county);
                            store.commit('setAddressUserDetail', item.address);
                        }
                    });

                } else {
                    console.log('当前存在默认地址数据', store.getters.getAddressId);
                }
            };

            // 更改支付方式
            const onPayMethod = (index)=>{
                selectIndex.value = index;
                $('._payMethod li:eq('+index +')').addClass('active').siblings('li').removeClass('active');
            };

            // 打开支付窗口
            const payFee = () => {
                let loadingInstance_init = ElLoading.service({fullscreen: false, text: '支付加载中', target: '._order-box'});
                handleCreateOrder();               // 检测是否获取支付面板
                if (loadingInstance_init !== null) {
                    loadingInstance_init.close();                       // 页面加载中...
                    loadingInstance_init = null;
                }
            };
            // 关闭支付窗口
            const tCancel = () => {
                $('#myModal').removeClass('show').css('display', 'none');
                $('#myModalBox').css('display', 'none');
                $('body').css('overflow', '');         // 重新启动滚动条
            };

            // 暂时取消订单
            const payCancel = () => {
                tCancel();
                ElMessage.error('注意！请及时支付订单');
                if(timer.value!==null){
                    clearInterval(timer.value);
                    timer.value = null;
                }
                setTimeout(() => {
                    router.go(-1);
                },500)
            };
            // 成功支付订单
            const submitPay = () => {
                tCancel();
                ElMessage.warning('已经确认支付，请等待支付结果');
                if(timer.value!==null){
                    clearInterval(timer.value);
                    timer.value = null;
                }
                setTimeout(() => {
                    router.go(-1);
                },500)
            };
            // 取消支付订单
            const submitPayCancel = () => {
                tCancel();
                ElMessage.warning('已经取消支付！请及时支付订单');
                if(timer.value!==null){
                    clearInterval(timer.value);
                    timer.value = null;
                }
                setTimeout(() => {
                    router.go(-1);
                },500)
            };

            // 监听支付结果
            const monitorPayResult = () =>{
                var maxTimeout = 10;
                var turns = 0;
                timer.value = setInterval(() => {
                    payOrderStatus(state.orderNo).then(res => {
                        if (res == '2') {
                            console.log('成功支付定单');
                            ElMessage.success('太棒啦！支付定单成功');
                            if(timer.value!==null){
                                clearInterval(timer.value);
                                timer.value = null;
                            }
                            $(".modal-footer .confirmPay").click();
                            // router.push({path:'/order',query:{status:2}});
                            router.push({path: '/order', query: {id: state.orderNo}});
                        } else {
                            console.log('等待支付中');
                        }
                        if(timer.value !== null){
                            turns++;
                            console.log("等待时间", turns);
                            if (turns >= maxTimeout) {
                                clearInterval(timer.value);
                                timer.value = null;
                                ElMessage.error('支付超时！请重新打开支付面板');
                                $(".modal-footer .cancelPay").click()
                            }
                        }
                    })
                }, 2000)
            };

            // 打开创建订单支付
            const handleCreateOrder = () => {

                var selectIconAll = ['icon-weixin','icon-alipay',''];
                var selectNameAll = ['微信','支付宝','QQ钱包'];
                mystatus.value = false;
                const params = {
                    address_id: state.address.id
                };
                createOrder(params).then(res => {
                    store.dispatch('updateCart'); // 更新购物车中的数据
                    state.showPay = true;
                    // 订单ID
                    state.orderNo = res.id;
                    payOrder(state.orderNo, {type: 'aliyun'}).then(res => {
                        state.aliyun = 'https://api.shop.eduwork.cn' + res.qr_code_url;
                        console.log('已经成功打开支付面板');
                        mystatus.value = true;

                        if(mystatus.value){
                            selectIcon.value = selectIconAll[selectIndex.value];       // 修改支付面板
                            selectName.value = selectNameAll[selectIndex.value];
                            console.log('可以开始支付');
                            setTimeout(() => {
                                $('#myModalBox').css('display', 'block');
                                $('#myModal').addClass('show').css('display', 'block');
                                $('body').css('overflow', 'hidden');         // 禁用滚动条
                                monitorPayResult();                     // 检测支付结果
                            },500);
                        }
                    }).catch(err=>{
                        console.log('获取支付接口出错',err);
                        ElMessage.error('获取支付接口出错');
                        mystatus.value = false;
                    });
                   /* payOrder(state.orderNo, {type: 'wechat'}).then(res => {
                        state.wechat = 'https://api.shop.eduwork.cn' + res.qr_code_url;
                        console.log(res);
                    });*/
                }).catch(err=>{
                    console.log('打开支付面板异常createorder-createorder-err',err);
                    ElMessage.error('获取支付接口出错');
                    return false;
                });
            };
            const total = computed(() => {
                let sum = 0;
                for (var i in state.carList) {
                    var item = state.carList[i];
                    sum += parseInt(item.num) * parseFloat(item.goods.price);
                }
                return sum;
            });
            return {payFee, payCancel, submitPay, submitPayCancel,
                ...toRefs(state), total, onPayMethod, selectIcon, selectName}
        }
    }

</script>

<style lang="scss">
    ._shoporder {
        min-height: 600px;

        #_order-box {
            max-width: 1200px;
            border: 1px solid red;

            ._shopItemBox {
                border-radius: 10px;
                border: 3px solid #F3E3DE;
                width: 99%;
                margin: auto;
                position: relative;
            }

            ._totalBalance {
                position: fixed;
                bottom: 0;
                left: 0;
                right: 0;
                border-top: 3px solid red;
                z-index: 90;
            }

            ._payMethod {
                border: 3px solid #f5f2f0;

                li > div._pay-list-item > i.iconfont {
                    display: none;
                }

                li.active > div._pay-list-item > i.iconfont {
                    display: inline;
                }
            }


        }

        .txt-subtitle-space {
            letter-spacing: 1.5px;
        }

        .txt-body-space {
            letter-spacing: 1px;
        }

    }

    #myModalBox {
        display: none;
        top: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 101;
        background: rgba(182, 175, 175, 0.5);

        #myModal {
            display: none;
            z-index: 102;
        }
    }

    .modal-content {
        max-width: 400px !important;
        max-height: 480px !important;
    }
</style>