import {request} from './request';

// 创建定单    (createorder)
export function createOrder(params){
    return request({
        url:'/api/orders',
        method:'post',
        params
    })
}
// 定单支付,获取二维码     (createorder)
export function payOrder(order,params){
    return request({
        url:`/api/orders/${order}/pay`,
        method:'get',
        params
    })
}

// 定单状态 (createorder)
export function payOrderStatus(order){
    return request({
        url:`/api/orders/${order}/status`, 
    })
}
// 确认收货 ， ---        order 订单id
export function confirmOrder(order){
    return request({
        url:`/api/orders/${order}/confirm`,
        method:'patch',
    })
}

// 获取定单预览， (createorder)
export function getOrderPreview(){
    return request({
        url:'/api/orders/preview',
        method:'get',
    })
}

// 获取定单列表 {page:1,status:2,include:'user,orderDetail.goods '}  (order)
export function getOrderList(params){
    return request({
        url:'/api/orders',
        method:'get',
        params
    })
}
// 获取定单详情  (orderdetail)
export function getOrderDetail(order){
    return request({
        url:`/api/orders/${order}`,
        method:'get',
        params:{
            include:'user,orderDetails.goods'
        }
    })
}

// 等待完善的内容
// 获取物流信息 ---
export function getViewExpress(order){
    return request({
        url:`/api/orders/${order}/express`,
        method:'get',
    })
}
// 评价商品信息 ---  order 订单id {goods_id:   , content: , rate:1/2/3   , star: 0~5 }
export function commentGoods(order,data){
    return request({
        url:`/api/orders/${order}/comment`,
        method:'post',
        data
    })
}